.App {
  background: url("./assets/img/bg2.jpg");
  background-size: cover;
}

.App-header {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navbar {
  padding: 5px;
  background-color: rgb(49, 46, 46, 0.6);
}
.navcontain {
  margin-top: 10px;

  display: flex;
  margin-left: 25px;
  margin-right: 25px;
  padding: 3px;
  justify-content: space-between;
  text-align: center;
  font-size: medium;
  font-weight: 600;
  color: rgb(255, 255, 255);
}

a {
  padding: 17px;
  color: white;
  text-decoration: none;
  outline: none;
}
a:hover {
  color: rgb(153, 159, 163);
}

.movie-container {
  margin-top: 20px;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 50px;
}

.movie-wrapper {
  text-align: center;
  width: 360px;
  color: black;
  background-color: rgb(209, 208, 209);
  border-radius: 8px;
}

.card {
  display: flex;
  height: auto;
  text-align: center;
  justify-content: center;
}

img {
  width: auto;
  margin-top: 10px;
  height: 28rem;
}

.card-title {
  height: 60px;
}

.movie-detail {
  font-size: medium;
  font-weight: 500;
  line-height: 8px;
}
.movie-search {
  margin-bottom: 30px;
  padding: 13px;
  font-size: medium;
  font-weight: 600;
  width: 300px;
}
